	module.exports = {"data":{"markdownRemark":{"html":"<p>I saw a lot of big projects/repositories with hundreds or even thousands of stale branches where nothing really happens.\nUsually, no one cares to clean up all this mess. There is a nice simple GitHub tweak where you can prevent this.</p>\n<h2 id=\"automatically-delete-merged-branches\"><a href=\"#automatically-delete-merged-branches\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Automatically delete merged branches</h2>\n<p>Github branches can be automatically deleted after press on the merge button.</p>\n<p>You need to be a <strong>repository admin</strong>:</p>\n<ol>\n<li>Navigate to repository <strong>Settings</strong></li>\n</ol>\n<p><a href=\"https://imgur.com/gICbc6V\"><img src=\"https://i.imgur.com/gICbc6Vl.png\" title=\"source: imgur.com\"></a></p>\n<ol start=\"2\">\n<li>Scroll down to the <strong>\"Merge button\"</strong> section</li>\n<li>Activate <strong>Automatically delete head branches</strong> checkbox</li>\n</ol>\n<p><a href=\"https://imgur.com/Tek0MBm\"><img src=\"https://i.imgur.com/Tek0MBml.png\" title=\"source: imgur.com\"></a></p>\n<p>Merged something accidentally and want your branch back? Don't worry, you'll be able to restore deleted branched.</p>","timeToRead":1,"excerpt":"I saw a lot of big projects/repositories with hundreds or even thousands of stale branches where nothing really happens.\nUsually, no oneâ€¦","frontmatter":{"title":"How to delete GitHub branches automatically after merge by default","cover":null,"date":"2019-11-14","category":"tech","tags":["coding","open source","code","dev","github","dev tools","git"],"author":"alex"},"fields":{"slug":"/how-to-delete-git-hub-branches-automatically-after-merge-by-default"}},"prev":{"excerpt":"How to invest in ETF? Hi there! Recently I've started to do some research on ETF topic and how I can invest inâ€¦","frontmatter":{"title":"Three simple steps to invest in ETF","cover":"https://images.unsplash.com/photo-1459257831348-f0cdd359235f?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=7e537922f712f142b24cf9c897f0ab05&auto=format&fit=crop&w=2550&q=80","date":"2018-07-25"},"fields":{"slug":"/three-simple-steps-to-invest-in-etf"}},"next":{"excerpt":"Renovate  - is a tool that does automated dependency updates for your project and the nicest thing is that it'sâ€¦","frontmatter":{"title":"ðŸ¤– How to configure Renovate - automated dependencies updates for your project","cover":null,"date":"2019-11-11"},"fields":{"slug":"/ðŸ¤–-how-to-configure-renovate-automated-dependencies-updates-for-your-project"}},"authors":{"edges":[{"node":{"id":"alex","name":"Alexander Grischuk","image":"https://www.dropbox.com/s/ifvhc39k53udz1e/profile-pic-size.jpg?raw=1","url":"https://www.linkedin.com/in/alxgi/","bio":"Frontend Engineer at Signavio. I like to build apps with React while keeping good UX in mind."}}]}},"pathContext":{"slug":"/how-to-delete-git-hub-branches-automatically-after-merge-by-default","total":12,"prev":"/three-simple-steps-to-invest-in-etf","next":"/ðŸ¤–-how-to-configure-renovate-automated-dependencies-updates-for-your-project"}}

